import React from 'react';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';

// [] props onDelete 
function SortableItem({ id, children, isSelected, onClick, onDelete, userClasses }) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.5 : 1,
    border: isSelected ? '2px solid #4A90E2' : '1px solid #ddd',
    boxShadow: isSelected ? '0 0 5px rgba(74, 144, 226, 0.5)' : 'none',
    cursor: 'pointer',
  };

  // []    
  const handleDeleteClick = (e) => {
    //     onClick   
    e.stopPropagation();
    onDelete(id);
  };

  return (
      <div
          ref={setNodeRef}
          style={style}
          {...attributes}
          {...listeners}
          onClick={onClick}
          className={`sortable-item ${userClasses || ''}`.trim()} //   position: relative .
      >
        {children}
        {isSelected && ( // isSelected true  X  
            <button className="delete-item-btn" onClick={handleDeleteClick}>
              X
            </button>
        )}
      </div>
  );
}

export default SortableItem;
