import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

const CafeBoard = () => {
  const dummyData = [
    { title: "   ", author: "John Doe", date: "2023-12-13" },
    { title: "React  ", author: "Jane Doe", date: "2023-12-12" },
    { title: "   ?", author: "", date: "2023-12-11" },
    { title: "  ", author: "John Smith", date: "2023-12-10" },
    { title: "   ?", author: "Emily Davis", date: "2023-12-09" },
    { title: "   ", author: "Michael Brown", date: "2023-12-08" },
    { title: "   ?", author: "Sarah Wilson", date: "2023-12-07" },
  ];

  return (
    <div className="cafe-board-container">
      <table className="table table-striped">
        <thead>
          <tr>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          {dummyData.map((item, index) => (
            <tr key={index}>
              <td>{item.title}</td>
              <td>{item.author}</td>
              <td>{item.date}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default CafeBoard;
